.cart {
    background-color: $dark;
    color: #fff;
    position: fixed;
    top: 0;
    right: -340px;
    width: 340px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 40px 0;
    transition: all .2s ease-in-out;
    
    &--open {
        right: 0;
    }

    &__icon--close {
        position: absolute;
        right: 30px;
        top: 40px;
        width: 30px;
        height: 30px;
        padding: 5px;
        cursor: pointer;
    }

    &__btn {
        display: block;
        align-self: center;
    }
}