.sort-select {
    font-family: Roboto;
    border: none;
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
    text-align: right;
    letter-spacing: 0.7px;

    .react-select-container {
        width: 130px;

        &:focus {
            outline: unset;
        }
    }
    .react-select__control {
        border: none;
        box-shadow: unset;

        &:hover {
            border: none;
        }
        &:focus {
            outline: unset;
        }
    }

    .react-select__control.react-select__control--menu-is-open {
        .react-select__indicator.react-select__dropdown-indicator {
            transform: rotate(180deg);
        }
    }

    .react-select__indicator-separator {
        display: none;
    }

    .react-select__indicator.react-select__dropdown-indicator {
        padding: 0;
        transition: all .2s ease-in-out;
    }

    .react-select__menu {
        border: 1px solid #000;
        border-radius: unset;
        min-width: 190px;
        font-weight: 300;
        font-size: 14px;
        line-height: 16px;
        text-align: right;
        letter-spacing: 0.7px;
        padding: 15px 0;
    }
}