.btn {
    text-transform: uppercase;
    padding: 20px;
    font-size: 16px;
    letter-spacing: 2px;
    display: block;
    background: #fff;
    border: 2px solid #000;
    cursor: pointer;

    &:disabled {
        background: #fff;
    }

    &--white {
        border: 2px solid #fff;
        color: #fff;
        background: transparent;
    }

    &-lg {
        min-width: 260px;
    }
}