.side-filter {
    @media screen and (max-width: $lg) {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }

    @media screen and (max-width: $sm) {
        justify-content: center;
    }

    &__list {
        padding-left: 0px;
        @media screen and (max-width: $lg) {
            display: flex;
        }
        .category {
            padding: 10px;
            margin: 5px 0;
            font-weight: 300;
            font-size: 12px;
            letter-spacing: .6px;
            text-transform: uppercase;
            cursor: pointer;

            @media screen and (max-width: $lg) {
                padding: 10px 5px;
            }

            &:hover {
                font-weight: 500;
            }

            &-active {
                font-weight: 500;
            }
        }
    }
}