.product {
    &__content {
        display: flex;

        @media screen and (max-width: $md) {
            flex-direction: column-reverse;
        }
        
    }

    &__img {
        width: 36%;
        height: 100vh;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        // position: absolute;
        // top: 0;
        margin-top: -63px;

        @media screen and (max-width: $xl) {
            min-width: 300px;
            height: 420px;
            margin: 40px auto ;
        }
    }

    &__desc {
        width: 64%;
        padding: 0 100px;

        @media screen and (max-width: $lg) {
            padding: 0 40px;
        }
        @media screen and (max-width: $md) {
            width: 100%;
        }
        @media screen and (max-width: $sm) {
            padding: 0 15px;
        }
    }

    &__link-back {
        position: relative;
        padding-left: 15px;
        font-size: 12px;
        letter-spacing: .6px;
        text-transform: uppercase;
        font-weight: 300;
        margin-bottom: 60px;
        display: block;

        &:before {
            position: absolute;
            content: '';
            top: 50%;
            left: 0;
            transform: translate(0, -55%);
            background-image: url('../../../images/icons/back.svg');
            width: 5px;
            height: 11px;
        }
    }

    &__category {
        font-weight: 500;
        font-size: 10px;
        letter-spacing: 1px;
        color: $grey;
        text-transform: uppercase;
    }

    &__name {
        font-weight: 500;
        font-size: 40px;
        margin-bottom: 30px;

        @media screen and (max-width: $md) {
            font-size: 25px;
        }
    }

    &__btns {
        margin-bottom: 60px;

        @media screen and (max-width: $sm) {
            .btn {
                margin: auto;
            }
        }
    }

    &__info {
        display: flex;

        @media screen and (max-width: $xl) {
            flex-wrap: wrap;
        }

        p {
            margin-bottom: 40px;
            margin-top: 0;
        }
    }

    &__text {
        width: 50%;
        margin-right: 104px;

        @media screen and (max-width: $xl) {
            width: 100%;
            margin-right: 0;
        }
    }

    &__properties__item {
        display: flex;
        align-items: center;
        white-space: nowrap;

        span {
            display: block;
            min-width: 100px;
            font-weight: 500;
            font-size: 10px;
            line-height: 12px;
            letter-spacing: 1px;
            text-transform: uppercase;
            color: $grey;
        }
    }

}