.header {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 20px 120px;
    position: relative;

    &__cart {
        display: flex;
        cursor: pointer;
        width: 40px;
    }

    &__logo {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        height: 40px;

        @media screen and (max-width: $md) {
            left: 40px;
            transform: translate(0, -50%);
        }

    }

    &__product-counter {
        min-width: 20px;
        height: 20px;
        background: #000;
        color: #fff;
        padding: 2px 6px;
        margin-left: 8px;
        font-weight: 500;
        font-size: $font-main;
    }
}