.product-item-wrapper {
    width: 33.3%;
    padding: 15px 40px;
    
    @media screen and (max-width: $xl) {
        width: 50%;
    }

    @media screen and (max-width: $md) {
        width: 100%;
    }
}

.product-item {
    width: 100%;
   
    cursor: pointer;
    transition: all .2s ease-in-out;
    color: $primary;

    &:hover {
        box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.0697115);
    }

    &__desc {
        padding: 15px 20px;
    }

    &__img {
        height: 390px;
        width: 100%;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;

        @media screen and (max-width: $lg) {
            height: 300px;
        }
    }

    &__category {
        font-weight: 500;
        font-size: 10px;
        letter-spacing: 1px;
        color: $grey;
        text-transform: uppercase;
    }

    &__name {
        font-weight: 300;
        line-height: 1.2;
    }

    &__price,
    &__available {
        text-align: center;
    }

    &__price {
        font-weight: 500;
        font-size: 20px;
        line-height: 23px;
        letter-spacing: 1px;
        margin-top: 15px;
    }

    &__available {
        font-weight: 300;
        font-size: 10px;
        letter-spacing: 1px;
        color: $grey;
    }
}